<template>
    <yd-layout :class="'agencyGround'">
        <div v-if="alFlag">
            <div class="mainContentFluid">
                <div class="fluidWidth">
                    <div class="advice">
                        <div class="commonAdvice">
                            常用意见：
                        </div>
                        <div class="adviceBtn">
                            <span class="selectBtn" v-bind:class="{selectActive:statusRadio==1}" v-on:click="statusRadio=1;opinion='同意'">同意</span>
                            <span class="selectBtn lSpace" v-bind:class="{selectActive:statusRadio==2}" v-on:click="statusRadio=2;opinion='不同意'">不同意</span>
                        </div>
                    </div>
                </div>
            </div>
            <div class="mainContentFluid">
                <div class="agent1">
                    <div class="adviceTxt">
                        <span slot="left" class="adviceLeft">其他意见：</span>
                        <trsTextarea id="js_otherAdvice" placeholder="请输入意见内容" v-model="opinion"></trsTextarea>
                    </div>
                </div>
            </div>
        </div>
        <yd-tabbar slot="tabbar">
            <div class="assistBottom">
                <div class="examineLogo btnOne">
                    <yd-flexbox>
                        <div class="examineLogo_txt toBack" @click="toBack(2)">返回</div>
                    </yd-flexbox>
                </div>
                <div id="js_agentLeader" class="examineLogo btnTwo" @click="sendBtn()">
                    <yd-flexbox>
                        <div class="examineLogo_txt">发送</div>
                    </yd-flexbox>
                </div>
            </div>
        </yd-tabbar>
    </yd-layout>
</template>
<script>
import { mapGetters } from 'vuex';
export default {
    name: 'leadAgency',
    data() {
        return {
            opinion: '',
            statusRadio: 99,
            suggtion: '',
            asFlag: false,
            alFlag: true,
            taFlag: false,
            userid: this.$route.params.userid
        }
    },
    props: ['approveJson'],
    mounted() {
        document.setTitle('出差审批');
    },
    computed: {
        ...mapGetters({
            userInfo: 'getUserInfo'
        })
    },
    methods: {
        toBack(type) {
            this.$emit('botSetflag', type);
        },
        sendBtn() {
            let vm = this;
            if (vm.opinion == '') {
                vm.$dialog.toast({
                    mes: '请输入意见内容',
                    timeout: 2000
                });
            } else {
                vm.get(vm.conf.portalMobile + '/API/WorkFlow/oawf/doOANext', {
                    'type': 'travelremark',
                    'workItemID': vm.$route.params.workItemID,
                    'token': `${vm.userInfo.ssotoken}L/wp-2q${vm.userInfo.ssotokenCode}`,
                    'activityID': 'travelSecrataryActivity2',
                    'leaderIDs': '',
                    'fuyan': '',
                    'opinion': vm.opinion,
                    'workTitle': vm.approveJson.firstorgname + vm.approveJson.applicantname + '出差备案申请',
                    'ext': ''
                }, function (res) {
                    vm.$dialog.toast({
                        mes: '发送成功',
                        timeout: 2000,
                        callback: () => {
                            vm.$router.push('/handle');
                        }
                    });
                });
            }
        }
    }
}
</script>
<style scoped>
.mainContentFluid:first-child {
    margin-top: 0;
    border-top: none;
}

.mainContentFluid {
    margin-top: 0;
    border-top: 0.1rem solid #eaeaea;
}

.advice {
    line-height: 0.68rem;
    height: 1.5rem;
    border-bottom: none;
}

.agent1 {
    padding: 0 .33rem;
    padding-top: .27rem;
    padding-bottom: .27rem;
}

.toTop {
    margin-top: 0.3rem;
}

.fluidWidth {
    padding-left: 0;
    padding-right: 0;
}

.adviceLeft {
    font-size: 0.32rem;
    color: #333;
    height: 0.6rem;
    line-height: 0.6rem;
    display: block;
}

.agencyGround {
    background-color: #fff;
}

.commonAdvice {
    color: #333 !important;
    width: 100%;
    float: none;
}

.selectBtn {
    display: inline-block;
    width: auto;
    padding: 0 .29rem;
    height: .5rem;
    line-height: .5rem;
    text-align: center;
    color: #333;
    font-size: .3rem;
    border-radius: .22rem;
    border: 1px solid #eaeaea;
    margin-top: 0.2rem;
}

.adviceBtn {
    margin-left: 0.22rem;
}

.lSpace {
    margin-left: 0.33rem;
}

.trsTextarea {
    font-size: 0.32rem;
    color: #999;
    min-height: 2rem;
    margin-top: 0.2rem;
}

.assistBottom {
    border-top: none;
}

.examineLogo {
    height: 0.99rem;
    margin-top: 0;
    border-right: none;
}

.examineLogo .yd-flexbox {
    line-height: 0.99rem;
}

.examineLogo_txt {
    color: #fff;
    font-size: 0.36rem;
    font-weight: normal;
}

.btnOne {
    background: #fafafa;
    letter-spacing: 0.1rem;
    border: 1px solid #eaeaea;
}

.btnTwo {
    background-color: #46a4f0;
}

.yd-tabbar {
    padding: 0;
}

.yd-tabbar:after {
    border-top: none;
}

.toBack {
    color: #333;
}

.selectActive {
    background-color: #61b8ff;
    color: #fff;
    border: none;
}

..yd-scrollview {
    background-color: #fff;
}
</style>


