<template>
    <yd-layout>
        <div class="mainContentFluid selectList" v-if="liuyanFlag" v-show="taFlag">
            <div class="fluidWidth fluidWidth2">
                <yd-cell-group>
                    <yd-cell-item>
                        <span slot="left" class="lastPost">上一则附言：</span>
                        <div class="fyColor" slot="right" v-text="liuyan"></div>
                    </yd-cell-item>
                </yd-cell-group>
            </div>
        </div>
        <div class="mainContentFluid" v-show="taFlag">
            <div class="fluidWidth fluidWidthApro">
                <div class="holiday_one">
                    <yd-flexbox direction="vertical">
                        <yd-flexbox-item>
                            <div class="hint">
                                {{formData.firstorgname}}{{formData.applicantname}}出差审批
                            </div>
                            <div class="waitApprove" v-text="approveText">
                            </div>
                        </yd-flexbox-item>
                    </yd-flexbox>
                </div>
            </div>
        </div>
        <div class="mainContent" v-show="taFlag">
            <div class="contentSradius holiday secondPart">
                <div class="holiday_two">
                    <yd-flexbox direction="vertical">
                        <yd-flexbox-item>
                            <yd-flexbox>
                                <div class="leftWord">
                                    出差地点：
                                </div>
                                <div class="rightWord" v-text="formData.travelplace"></div>
                            </yd-flexbox>
                        </yd-flexbox-item>
                        <yd-flexbox-item v-if="traveldays!=''">
                            <yd-flexbox>
                                <div class="leftWord tripTime">
                                    出差时间：
                                </div>
                                <div class="rightWord rightTime">{{startdate}}至{{enddate}}({{traveldays}})天</div>
                            </yd-flexbox>
                        </yd-flexbox-item>
                        <yd-flexbox-item v-if="traveldays==''">
                            <yd-flexbox>
                                <div class="leftWord tripTime">
                                    出差时间：
                                </div>
                                <div class="rightWord rightTime">{{startdate}} 至 {{enddate}}</div>
                            </yd-flexbox>
                        </yd-flexbox-item>
                        <yd-flexbox-item v-if="formData.othertravelnames!=''">
                            <yd-flexbox>
                                <div class="leftWord tripTime">
                                    随行人员：
                                </div>
                                <div class="tripPerson persons" v-text="formData.othertravelnames"></div>
                            </yd-flexbox>
                        </yd-flexbox-item>
                    </yd-flexbox>
                </div>
            </div>
        </div>
        <div class="mainContent lastMain" v-show="taFlag">
            <div class="contentSradius holiday">
                <div class="holiday_two tripText">
                    <yd-flexbox direction="vertical">
                        <yd-flexbox-item v-if="reason!=''">
                            <div class="leftWord">
                                出差事由：
                            </div>
                            <div class="partText" v-html="transferString(reason)"></div>
                        </yd-flexbox-item>
                        <yd-flexbox-item v-if="reason2!=''">
                            <div class="leftWord">
                                行程安排：
                            </div>
                            <div class="textColor" v-html="transferString(reason2)"></div>
                        </yd-flexbox-item>
                    </yd-flexbox>
                </div>
            </div>
        </div>
        <div class="mainContentFluid lastPart" v-if="aproveList != ''" v-show="taFlag">
            <div class="fluidWidth leaderAdvice">
                <yd-flexbox direction="vertical">
                    <yd-flexbox-item>
                        <yd-flexbox>
                            <div class="adviceTitle">领导审批意见：</div>
                        </yd-flexbox>
                    </yd-flexbox-item>
                    <yd-flexbox-item>
                        <yd-flexbox>
                            <div class="advicePart" style="width:100%">
                                <div class="label_box" v-for="item in aproveList">
                                    <div class="range_txt attachment" v-if="item.userName1!=''">{{item.userName1}}：</div>
                                    <div class="range_txt attachment" v-text='item.neirong'></div>
                                    <div class="range_txt issueName">
                                        <div class="range issueTime" v-text='getDay(item.submitDate)'></div>
                                    </div>
                                </div>
                            </div>
                        </yd-flexbox>
                    </yd-flexbox-item>
                </yd-flexbox>
            </div>
        </div>
        <div class="last_send" v-show="asFlag">
            <assistant-send v-on:botSetflag="setFlag"></assistant-send>
        </div>
        <div class="last_send" v-show="alFlag">
            <lead-agency :approveJson="approveJson" v-on:botSetflag="setFlag"></lead-agency>
        </div>
        <yd-tabbar slot="tabbar" v-show="agentFlag != 'true' && status != 1 && taFlag==true">
            <div class="asBottom">
                <div class="otherL btnOne">
                    <yd-flexbox>
                        <div id="js_agreeBtn" class="examineLogo_txt sendBtn" v-text="agree" v-on:click="opstatus=1;opinion=agree;leaderApprove()"></div>
                    </yd-flexbox>
                </div>
                <div class="otherL btnTwo">
                    <yd-flexbox>
                        <div id="js_disagreeBtn" class="examineLogo_txt sendBtn" v-text="disagree" v-on:click="opstatus=0;opinion=disagree;leaderApprove()"></div>
                    </yd-flexbox>
                </div>
                <div class="circulationLogo otherL ">
                    <yd-flexbox id="js_otherBtn" v-on:click.native="show = true">
                        <div class="examineLogo_txt sendBtn">其他意见</div>
                    </yd-flexbox>
                </div>
            </div>
        </yd-tabbar>
        <yd-tabbar slot="tabbar" v-show="agentFlag == 'true' && taFlag==true && status == 0">
            <div class="asBottom">
                <div class="examineLogo btnSend">
                    <yd-flexbox id="js_agentLeaderBtn" v-on:click.native="setFlag(3)">
                        <div class="examineLogo_txt sendBtn">代领导录入</div>
                    </yd-flexbox>
                </div>
                <div class="examineLogo btnAgent">
                    <yd-flexbox id="js_approveSendBtn" @click.native="setFlag(1)">
                        <div class="examineLogo_txt sendBtn">发送</div>
                    </yd-flexbox>
                </div>
            </div>
        </yd-tabbar>
        <yd-popup v-model="show" position="center" width="5.79rem">
            <div class="other">其他意见</div>
            <yd-cell-group>
                <yd-cell-item>
                    <yd-textarea id="js_otherInput" slot="right" v-model="otherSuggest" placeholder="请您输入意见..."></yd-textarea>
                </yd-cell-item>
            </yd-cell-group>
            <div class="otherBtn">
                <ul>
                    <li class="yes" id="js_determineBtn" @click="opstatus=1;opinion=otherSuggest;leaderApprove()">同意</li>
                    <li class="no" id="js_cancelBtn" @click="opstatus=0;opinion=otherSuggest;leaderApprove()">不同意</li>
                </ul>
            </div>
        </yd-popup>
    </yd-layout>
</template>
<script>
import { mapGetters } from 'vuex';
import assistantSend from './assistantSend.vue';
import leadAgency from './leadAgency.vue';
export default {
    name: 'tripApprove',
    components: {
        assistantSend,
        leadAgency
    },
    data() {
        return {
            approveJson: {
                firstorgname: '',
                applicantname: ''
            },
            reason: '', // 出差事由
            reason2: '', // 行程安排
            traveldays: '', // 出差天数
            inputPs: '',
            agree: '同意',
            disagree: '不同意',
            show: false,
            showFlag: false,
            opinion: '',
            otherSuggest: '',
            formData: {},
            aproveList: [],
            startdate: '',
            enddate: '',
            userid: '',
            secrataryKey: '',
            secrataryKey2: '',
            secrataryKey3: '',
            keyActiveId: '',
            doKey: '',
            approveType: '',
            leaderIDs: '',
            approveText: '',
            liuyan: '',
            liuyanFlag: true,
            asFlag: false,
            alFlag: false,
            taFlag: true,
            agentFlag: this.$route.params.agentFlag,
            activityId: this.$route.params.activityId,
            status: this.$route.params.status,
            errorTime: 0,
            opstatus: -1
        }
    },
    mounted() {
        let vm = this;
        document.setTitle('出差审批');
        vm.getLast();
        vm.initForm();
        if (vm.status == 1) {
            vm.approveText = '您已完成审批';
        } else {
            vm.approveText = '等待您审批';
        }
        vm.$dialog.loading.open('加载中...');
    },
    computed: {
        ...mapGetters({
            userInfo: 'getUserInfo'
        })
    },
    methods: {
        setFlag(type) {
            let vm = this;
            if (type == 1) {
                vm.asFlag = true;
                vm.alFlag = false;
                vm.taFlag = false;
            } else if (type == 2) {
                vm.asFlag = false;
                vm.alFlag = false;
                vm.taFlag = true;
            } else if (type == 3) {
                vm.asFlag = false;
                vm.alFlag = true;
                vm.taFlag = false;
            }
        },
        // 上一则附言
        getLast() {
            let vm = this;
            vm.post(vm.conf.portalMobile + '/API/WorkFlow/oawf/oapreviousfuyan', {
                'type': 'travelremark',
                'workItemID': vm.$route.params.workItemID,
                'token': `${vm.userInfo.ssotoken}L/wp-2q${vm.userInfo.ssotokenCode}`
            }, function(res) {
                if (res.jsonData == '') {
                    vm.liuyanFlag = false;
                } else {
                    vm.liuyan = res.jsonData[0].neirong;
                }
            }, function(res) {}, false);
        },
        // 表单数据
        initForm() {
            var vm = this;
            var promise = new Promise(function(resolve, reject) {
                vm.getM(vm.conf.portalMobile + '/API/WorkFlow/oawf/queryForm', {
                    'type': 'travelremark',
                    'workItemID': vm.$route.params.workItemID,
                    'token': `${vm.userInfo.ssotoken}L/wp-2q${vm.userInfo.ssotokenCode}`
                }, function(res) {
                    if (res.isSuccess == 'false') {
                        vm.errorTime++;
                        if (vm.errorTime < 3) {
                            vm.initForm();
                        } else {
                            vm.$dialog.loading.close();
                            vm.$dialog.toast({
                                mes: '表单未加载成功',
                                timeout: 2000,
                                callback: () => {
                                    vm.$router.push('/handle');
                                }
                            });
                        }
                    } else {
                        vm.$dialog.loading.close();
                    }
                    if (res.jsonData.CURRENTSTATE != 10 && vm.$route.params.status == 0 && res.isSuccess == 'true') {
                        vm.$dialog.toast({
                            mes: '该流程已被处理',
                            timeout: 2000,
                            callback: () => {
                                setTimeout(function() {
                                    if (vm.$route.params.status == 0) {
                                        vm.$router.push('/handle');
                                    }
                                })
                            }
                        });
                        return false;
                    }
                    if (res.isSuccess == 'true') {
                        vm.aproveList = [...res.jsonData.applyopinions, ...res.jsonData.leaderopinions];
                        vm.aproveList.sort(function(a, b) {
                            let timeA = a.submitDate.replace(/-/g, '/');
                            let timeB = b.submitDate.replace(/-/g, '/');
                            return Date.parse(timeB) - Date.parse(timeA);
                        });
                        vm.formData = res.jsonData;
                        vm.reason = res.jsonData.reason;
                        vm.reason2 = res.jsonData.reason2;
                        vm.userid = res.jsonData.applicantid;
                        vm.traveldays = res.jsonData.traveldays;
                        vm.approveJson.applicantname = vm.formData.applicantname;
                        vm.approveJson.firstorgname = vm.formData.firstorgname;
                        vm.startdate = vm.formatDate(vm.formData.startdate, 'yyyy-MM-dd');
                        if (vm.formData.enddate != '' && vm.formData.enddate != null) {
                            vm.enddate = vm.formatDate(vm.formData.enddate, 'yyyy-MM-dd');
                        }
                        for (let key in vm.formData.nextBtn) {
                            if (key == 'travelSecrataryActivity2') {
                                vm.keyActiveId = key;
                                vm.leaderIDs = vm.formData.nextBtn.travelSecrataryActivity2[0];
                            }
                            if (key == 'travelSecrataryActivity3') {
                                vm.keyActiveId = key;
                                vm.leaderIDs = vm.formData.nextBtn.travelSecrataryActivity3[0];
                            }
                            if (key == 'travelSecrataryActivity') {
                                vm.keyActiveId = key;
                                vm.leaderIDs = vm.formData.nextBtn.travelSecrataryActivity[0];
                            }
                            if (key.indexOf('doApprove') != -1) {
                                vm.doKey = key;
                                vm.approveType = vm.formData.nextBtn.doApprove.type;
                                for (let keyId in vm.formData.nextBtn.doApprove) {
                                    if (keyId != 'type') {
                                        vm.leaderIDs = keyId;
                                    }
                                }
                            }
                        }
                    }
                    resolve(true);
                }, function(res) {
                    if (res.response.status != 200) {
                        vm.$dialog.loading.close();
                        vm.$dialog.toast({
                            mes: '表单未加载成功',
                            timeout: 2000,
                            callback: () => {
                                vm.$router.push('/handle');
                            }
                        });
                    }
                });
            });
            promise.then(function(data) {
                vm.showFlag = true;
            });
        },
        // 领导审批按钮
        leaderApprove() {
            let vm = this;
            if (vm.show == true && vm.opinion == '') {
                vm.$dialog.toast({
                    mes: '请您输入意见',
                    timeout: 2000
                });
            } else if (vm.doKey == 'doApprove') {
                vm.show = false;
                vm.get(vm.conf.portalMobile + '/API/WorkFlow/oawf/doApprove', {
                    'type': 'travelremark',
                    'approveType': vm.approveType,
                    'workItemID': vm.$route.params.workItemID,
                    'token': `${vm.userInfo.ssotoken}L/wp-2q${vm.userInfo.ssotokenCode}`,
                    'leaderIDs': vm.leaderIDs,
                    'fuyan': '',
                    'opinion': vm.opinion,
                    'workTitle': vm.formData.firstorgname + vm.formData.applicantname + '出差备案申请',
                    'ext': '{"opstatus":"' + vm.opstatus + '"}'
                }, function(res) {
                    vm.$dialog.toast({
                        mes: '审批完成',
                        timeout: 2000,
                        callback: () => {
                            vm.$router.push('/handle/');
                        }
                    });
                });
            } else {
                vm.show = false;
                vm.get(vm.conf.portalMobile + '/API/WorkFlow/oawf/doOANext', {
                    'type': 'travelremark',
                    'workItemID': vm.$route.params.workItemID,
                    'activityID': vm.keyActiveId,
                    'token': `${vm.userInfo.ssotoken}L/wp-2q${vm.userInfo.ssotokenCode}`,
                    'leaderIDs': vm.leaderIDs,
                    'fuyan': '',
                    'opinion': vm.opinion,
                    'workTitle': vm.formData.firstorgname + vm.formData.applicantname + '出差备案申请',
                    'ext': ''
                }, function(res) {
                    vm.$dialog.toast({
                        mes: '审批完成',
                        timeout: 2000,
                        callback: () => {
                            vm.$router.push('/handle/');
                        }
                    });
                });
            }
        },
        toPage() {
            this.$router.push('/leadAgency/' + this.userid);
        },
        getDay(date) {
            date = date.replace(/-/g, '/');
            return (new Date(date).getMonth() + 1) + '月' + new Date(date).getDate() + '日';
        }
    }
}
</script>
<style scoped>
.mainContentFluid {
    margin-top: 0;
}

.fluidWidthApro {
    padding-bottom: 0;
}

.holiday_two {
    border-bottom: none;
    margin: 0;
}

.holiday_two .yd-flexbox-item:last-child {
    border-bottom: none;
}

.lastPost {
    color: #333;
}

.yd-cell-box {
    margin-bottom: 0;
}

.selectList {
    margin-top: 0;
}

.fluidWidth2 {
    padding: 0;
    padding-left: 0.06rem;
}

.asBottom {
    z-index: 99;
    border-top: none;
    width: 100%;
}

.lastPart {
    padding-bottom: 0;
    margin-top: 0;
    height: auto;
}

.otherL {
    width: 33.33%;
}

.sendBtn {
    margin-top: 0;
}

.hint {
    font-size: 0.34rem;
    line-height: 0.8rem;
}

.waitApprove {
    font-size: 0.3rem;
}

.holiday_one {
    margin: 0;
    border-bottom: none;
    height: 1.43rem;
}

.holiday_one .yd-flexbox-item:last-child {
    padding-top: 0;
}

.rightWord,
.leftWord {
    font-size: 0.32rem;
}

.rightWord {
    padding-left: 0;
}

.inputRemark {
    font-size: 0.3rem;
    min-width: 6.2rem;
    max-width: 3rem;
    min-height: 2rem;
}

.leaderAdvice {
    padding-right: 0;
    padding-left: 0;
    min-height: 2rem;
}

.secondLine {
    margin-left: 1.8rem;
}

.leaderAdvice .yd-flexbox-item {
    border-bottom: none;
    min-height: 0.72rem;
}

.yd-tabbar {
    padding: 0;
}

.yd-tabbar:after {
    border-top: 0;
}

.attachment {
    color: #333;
    max-width: 7.5rem;
    float: left;
    margin: 0;
    width: auto;
    margin-bottom: .1rem;
}

.label_box {
    overflow: hidden;
    height: auto;
    position: relative;
    margin-top: .2rem;
}

.issueTime {
    float: right;
    margin-top: 0;
}

.issueName {
    margin-left: 4rem;
    margin-top: 0;
    color: #0a0a0a;
    margin-bottom: .1rem;
}

.fl {
    float: left;
}

.last_send {
    height: 100%;
}

.fyColor {
    width: 100%;
    text-align: left;
    color: #999;
    font-size: 0.32rem;
    word-break: break-word;
}

.tripTime {
    width: 2rem;
    position: absolute;
    top: 0;
    left: 0;
}

.rightTime {
    margin-left: 1.6rem;
}

.holiday .yd-flexbox-item {
    line-height: 0.8rem;
    padding: 0;
    padding: 0 0.28rem;
    border-bottom: 1px dashed #eaeaea;
}

.unit {
    word-spacing: 0.24rem;
}

.partText {
    padding-bottom: 0.3rem;
    color: #333;
    font-size: 0.32rem;
    line-height: 0.6rem;
    word-break: break-all;
}

.textColor {
    color: #333;
    font-size: 0.32rem;
    line-height: 0.6rem;
    min-height: 0.8rem;
    word-break: break-all;
}

.mainContent {
    padding: 0 0.18rem;
    padding-top: 0.17rem;
    height: auto;
    /*margin-bottom: 0.17rem;*/
}

.contentSradius {
    margin-top: 0;
}

.assistBottom {
    height: 0.99rem;
}

.examineLogo {
    height: 0.99rem;
    margin-top: 0;
    border-right: none;
}

.examineLogo .yd-flexbox {
    line-height: 0.99rem;
}

.examineLogo_txt {
    color: #fff;
    line-height: 0.99rem;
    font-size: 0.36rem;
}

.btnOne,
.btnSend {
    background-color: #46a4f0;
}

.btnTwo,
.btnAgent {
    background-color: #5cc3d1;
}

.otherL {
    margin-top: 0;
    height: 0.99rem;
    border-right: none;
}

.circulationLogo {
    background-color: #8ac5f5;
}

.adviceTitle {
    font-size: 0.32rem;
    color: #999;
    padding-left: 0.13rem;
}

.lastMain {
    padding-bottom: 0.17rem;
}

.advicePart {
    min-height: 0.8rem;
}

.secondPart .yd-flexbox-item {
    min-height: .8rem;
}

.persons {
    line-height: 0.5rem;
    padding-top: 0.15rem;
}

.tripPerson {
    font-size: 0.32rem;
    color: #333;
    float: right;
    width: 4.5rem;
    margin-left: 1.8rem;
    /*min-height: 0.5rem;*/
    word-break: break-all;
    outline: none;
    user-select: text;
    white-space: pre-wrap;
    text-align: left;
}
</style>


