<template>
    <yd-layout :class="'sendGround'">
        <div v-if="asFlag">
            <div class="mainContentFluid selectList">
                <div class="fluidWidth fluidWidth2">
                    <yd-cell-group v-if="liuyanFlag">
                        <yd-cell-item>
                            <span slot="left" class="lastPost">上一则附言：</span>
                            <div slot="right" class="fyColor" v-text="liuyan"></div>
                        </yd-cell-item>
                    </yd-cell-group>
                    <div class="adviceTxt">
                        <yd-cell-group>
                            <yd-cell-item>
                                <span slot="left" class="leftText">填写附言</span>
                                <yd-textarea slot="right" v-model="fuyan" placeholder="请输入附言内容"></yd-textarea>
                            </yd-cell-item>
                        </yd-cell-group>
                    </div>
                </div>
            </div>
            <div class="mainContentFluid lastMain">
                <div class="fluidWidth agent1">
                    <yd-flexbox direction="vertical">
                        <yd-flexbox-item>
                            <div v-if="leaderKey == 'travelLeaderActivity'" class="leaderExamine" v-bind:class="{active:officeActive1==1}" v-on:click="officeActive1=1;activityID='travelLeaderActivity';leaderIDs=travelLeaderId;confirmItem2='';confirmItem3=''">送领导审批{{confirmItem1}}</div>
                            <div v-if="knowKey == 'travelKnownActivity'" class="leaderExamine" v-bind:class="{active:officeActive1==2}" v-on:click="activityID='travelKnownActivity';popupArr=authorizerList;statusPopup=true;statusRadio=999;leaderIDs='';officeActive1=999;confirmItem3=''">送授权审批人审批{{confirmItem2}}</div>
                            <div v-if="approveKey == 'travelApproveActivity'" class="leaderExamine" v-bind:class="{active:officeActive1==3}" v-on:click="officeActive1=3;activityID='travelApproveActivity';leaderIDs=approveLeaderIDs;confirmItem2='';confirmItem3=''">送办公室备案</div>
                            <div v-if="confirmKey == 'travelConfirmActivity'" class="leaderExamine" v-bind:class="{active:officeActive1==5}" v-on:click="officeActive1=5;activityID='travelConfirmActivity';leaderIDs=confirmLeaderIDs;confirmItem2='';confirmItem3=''">申请人确认</div>
                            <div v-if="secratary3Key == 'travelSecrataryActivity3'" class="leaderExamine" v-bind:class="{active:officeActive1==7}" v-on:click="officeActive1=7;activityID='travelSecrataryActivity3';leaderIDs=secratary3LeaderIDs;confirmItem2='';confirmItem3=''">审批完成</div>
                            <div v-if="secratary1Key == 'travelSecrataryActivity1'" class="leaderExamine" v-bind:class="{active:officeActive1==8}" v-on:click="activityID='travelSecrataryActivity1';popupArr02=secratary1List;statusPopup02=true;statusRadio=999;leaderIDs='';officeActive1=999;confirmItem2=''">送其他秘书审批{{confirmItem3}}</div>
                            <div v-if="applyKey == 'travelApplyActivity'" class="leaderExamine" v-bind:class="{active:officeActive1==4}" v-on:click="officeActive1=4;activityID='travelApplyActivity';leaderIDs=applyLeaderIDs;confirmItem2='';confirmItem3='';opinion='退回'">返回申请人{{confirmApply}}</div>
                        </yd-flexbox-item>
                    </yd-flexbox>
                </div>
            </div>
        </div>
        <yd-tabbar slot="tabbar" v-if="statusPopup==false&&statusPopup02==false">
            <div class="assistBottom">
                <div class="examineLogo btnOne">
                    <yd-flexbox>
                        <div class="examineLogo_txt toBack" @click="toBack(2)">返回</div>
                    </yd-flexbox>
                </div>
                <div id="js_agentAssistant" class="examineLogo btnTwo" v-on:click="sendBtn()">
                    <yd-flexbox>
                        <div class="examineLogo_txt">发送</div>
                    </yd-flexbox>
                </div>
            </div>
        </yd-tabbar>
        <yd-popup v-model="statusPopup" position="bottom" height="4rem">
            <div class="confirmBtn" slot="top">
                <div class="leftBtn" v-on:click="statusPopup = false">取消</div>
                <div class="rightBtn" v-on:click="statusPopup = false;">确定</div>
            </div>
            <div class="selectStatus">
                <ul class="ul">
                    <li class="li" v-for="(item,index) in popupArr" v-bind:class="{active:statusRadio==index}" v-on:click="statusRadio=index;itemname=item.empname;leaderIDs=item.operatorid;officeActive1=2;selectName()" v-text='item.empname'>
                    </li>
                </ul>
            </div>
        </yd-popup>
        <yd-popup v-model="statusPopup02" position="bottom" height="4rem">
            <div class="confirmBtn" slot="top">
                <div class="leftBtn" v-on:click="statusPopup02 = false">取消</div>
                <div class="rightBtn" v-on:click="statusPopup02 = false;">确定</div>
            </div>
            <div class="selectStatus">
                <ul class="ul">
                    <li class="li" v-for="(item,index) in popupArr02" v-bind:class="{active:statusRadio02==index}" v-on:click="statusRadio02=index;itemname=item.empname;leaderIDs=item.operatorid;officeActive1=8;selectName02()" v-text='item.empname'>
                    </li>
                </ul>
            </div>
        </yd-popup>
    </yd-layout>
</template>
<script>
import { mapGetters } from 'vuex';
export default {
    name: 'assistantSend',
    data() {
        return {
            officeActive1: 999,
            statusPopup02: false,
            inputNum: '',
            fuyan: '',
            leaderKey: '',
            knowKey: '',
            approveKey: '',
            applyKey: '',
            confirmKey: '',
            activityID: '',
            personID: '',
            leaderIDs: '',
            leaderQueryType: '',
            knownQueryType: '',
            leaderList: [],
            authorizerList: [],
            secratary1List: [],
            statusPopup: false,
            popupArr: [],
            popupArr02: [],
            statusRadio: 999,
            statusRadio02: 999,
            itemname: '',
            confirmItem1: '',
            confirmItem2: '',
            confirmApply: '',
            isType: '',
            applyLeaderIDs: '',
            approveLeaderIDs: '',
            confirmLeaderIDs: '',
            secratary1LeaderIDs: '',
            secratary3LeaderIDs: '',
            secratary1Key: '',
            secratary3Key: '',
            travelLeaderId: '',
            liuyanFlag: true,
            liuyan: '',
            asFlag: true,
            alFlag: false,
            taFlag: false,
            secratary1QueryType: '',
            firstorgname: '',
            applicantname: '',
            opinion: ''
        }
    },
    mounted() {
        document.setTitle('出差审批');
        this.getLast();
        this.formData();
    },
    computed: {
        ...mapGetters({
            userInfo: 'getUserInfo'
        })
    },
    methods: {
        toBack(type) {
            this.$emit('botSetflag', type);
        },
        getLast() {
            let vm = this;
            vm.post(vm.conf.portalMobile + '/API/WorkFlow/oawf/oapreviousfuyan', {
                'type': 'travelremark',
                'token': `${vm.userInfo.ssotoken}L/wp-2q${vm.userInfo.ssotokenCode}`,
                'workItemID': vm.$route.params.workItemID
            }, function (res) {
                if (res.jsonData == '') {
                    vm.liuyanFlag = false;
                } else {
                    vm.liuyan = res.jsonData[0].neirong;
                }
            }, function (res) {}, false);
        },
        // 获取表单中的btn
        formData() {
            let vm = this;
            vm.post(vm.conf.portalMobile + '/API/WorkFlow/oawf/queryForm', {
                'type': 'travelremark',
                'workItemID': vm.$route.params.workItemID,
                'token': `${vm.userInfo.ssotoken}L/wp-2q${vm.userInfo.ssotokenCode}`
            }, function (res) {
                let data = res.jsonData.nextBtn;
                vm.firstorgname = res.jsonData.firstorgname;
                vm.applicantname = res.jsonData.applicantname;
                for (let key in data) {
                    if (key.indexOf('travelLeaderActivity') != -1) {
                        vm.leaderKey = key;
                        vm.travelLeaderId = data.travelLeaderActivity[0];
                        vm.confirmItem1 = '（' + data.travelLeaderActivity[1] + '）';
                    } else if (key.indexOf('travelKnownActivity') != -1) {
                        vm.knowKey = key;
                        vm.knownQueryType = data[key][1];
                    } else if (key.indexOf('travelApproveActivity') != -1) {
                        vm.approveKey = key;
                        vm.approveLeaderIDs = data[key][0];
                    } else if (key.indexOf('travelApplyActivity') != -1) {
                        vm.applyKey = key;
                        vm.applyLeaderIDs = data[key][0];
                        vm.confirmApply = '（' + data.travelApplyActivity[1] + '）';
                    } else if (key.indexOf('travelConfirmActivity') != -1) {
                        vm.confirmKey = key;
                        vm.confirmLeaderIDs = data[key][0];
                    } else if (key.indexOf('travelSecrataryActivity3') != -1) {
                        vm.secratary3Key = key;
                        vm.secratary3LeaderIDs = data[key][0];
                    } else if (key.indexOf('travelSecrataryActivity1') != -1) {
                        vm.secratary1Key = key;
                        vm.secratary1QueryType = data[key][1];
                    }
                }
                vm.query();
            }, function (res) {}, false);
        },
        // 查询人员列表
        query() {
            let vm = this;
            // 查询授权审批人
            if (vm.knownQueryType != '') {
                vm.post(vm.conf.portalMobile + '/API/WorkFlow/oawf/handlerPerson', {
                    'type': 'travelremark',
                    'token': `${vm.userInfo.ssotoken}L/wp-2q${vm.userInfo.ssotokenCode}`,
                    'queryType': vm.knownQueryType
                }, function (res) {
                    let data = res.jsonData;
                    vm.authorizerList = data;
                }, function (res) {}, false);
            }
            if (vm.secratary1QueryType != '') {
                vm.post(vm.conf.portalMobile + '/API/WorkFlow/oawf/handlerPerson', {
                    'type': 'travelremark',
                    'token': `${vm.userInfo.ssotoken}L/wp-2q${vm.userInfo.ssotokenCode}`,
                    'queryType': vm.secratary1QueryType
                }, function (res) {
                    let data = res.jsonData;
                    vm.secratary1List = data;
                }, function (res) {}, false);
            }
        },
        sendBtn() {
            let vm = this;
            if (vm.leaderIDs == '') {
                vm.$dialog.toast({
                    mes: '请选择发送人',
                    timeout: 2000
                });
            } else {
                vm.get(vm.conf.portalMobile + '/API/WorkFlow/oawf/doOANext', {
                    'type': 'travelremark',
                    'workItemID': vm.$route.params.workItemID,
                    'token': `${vm.userInfo.ssotoken}L/wp-2q${vm.userInfo.ssotokenCode}`,
                    'activityID': vm.activityID,
                    'leaderIDs': vm.leaderIDs,
                    'fuyan': vm.fuyan,
                    'opinion': vm.opinion,
                    'workTitle': vm.firstorgname + vm.applicantname + '出差备案申请',
                    'ext': ''
                }, function (res) {
                    vm.$dialog.toast({
                        mes: '发送成功',
                        timeout: 2000,
                        callback: () => {
                            vm.$router.push('/handle');
                        }
                    });
                });
            }
        },
        selectName() {
            let vm = this;
            vm.confirmItem2 = '（' + vm.itemname + '）';
        },
        selectName02() {
            let vm = this;
            vm.confirmItem3 = '（' + vm.itemname + '）';
        }
    }
}
</script>
<style scoped>
.mainContentFluid {
    margin-top: 0;
}

.fluidWidth2 {
    padding: 0;
}

.fluidWidth {
    padding-left: 0;
    padding-right: 0;
}

.lastPost {
    color: #333;
    font-size: 0.32rem;
}

.yd-cell-box {
    border-bottom: 1px solid #eaeaea;
    margin-bottom: 0;
}

.agent1 .active:after {
    content: '';
    height: .45rem;
    width: .4rem;
    display: inline-block;
    background: url(../../../static/images/trs_up_03.png) no-repeat;
    background-position: 0 .14rem;
    background-size: 0.4rem;
    position: absolute;
    top: 0;
    right: 0.4rem;
}

.toTop {
    margin-top: 0.3rem;
}

.agent1 .yd-flexbox-item:last-child {
    padding: 0 0.2rem;
}

.yd-cell-item {
    padding-left: 0.1rem;
}

.leftText {
    color: #333;
    font-size: 0.32rem;
}

.sendGround {
    background-color: #fff;
}

.fyColor {
    width: 100%;
    text-align: left;
    color: #999;
    font-size: 0.32rem;
}

.assistBottom {
    border-top: none;
}

.examineLogo {
    height: 0.99rem;
    margin-top: 0;
    border-right: none;
}

.examineLogo .yd-flexbox {
    line-height: 0.99rem;
}

.examineLogo_txt {
    color: #fff;
    font-size: 0.36rem;
    font-weight: normal;
}

.btnOne {
    background: #fafafa;
    letter-spacing: 0.1rem;
    border: 1px solid #eaeaea;
}

.btnTwo {
    background-color: #46a4f0;
}

.yd-tabbar {
    padding: 0;
}

.yd-tabbar:after {
    border-top: none;
}

.toBack {
    color: #333;
}
</style>


